import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/menu-ca',
    name: 'MenuViewCa',
    component: () => import('../views/MenuViewCa.vue')
  },
  {
    path: '/menu-es',
    name: 'MenuViewEs',
    component: () => import('../views/MenuViewEs.vue')
  },
  {
    path: '/menu-eng',
    name: 'MenuViewEng',
    component: () => import('../views/MenuViewEng.vue')
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
